@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.card.income {
  border-color: #2ecc71; }

.card.expense {
  border-color: #e74c3c; }

body {
  font-family: 'Poppins', sans-serif;
  background-color: #fff;
  color: #000;
  line-height: 1.6; }

ul {
  list-style: none; }

.container {
  max-width: 768px;
  margin: auto;
  padding: 0 20px; }

header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px; }

.card {
  color: #fff;
  border-radius: 15px;
  padding: 40px 50px;
  margin: 20px 0;
  position: relative; }

.card.reverse {
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff; }

.card h2 {
  font-size: 22px;
  font-weight: 600;
  text-align: center; }

.amount-type {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 30px 0 40px;
  width: 100%; }

.amount-type li.income {
  background-color: #2ecc71; }

.amount-type li.expense {
  background-color: #e74c3c; }

.amount-type li,
.num-display {
  position: relative;
  width: 120px;
  height: 50px;
  padding: 10px;
  text-align: center;
  border-radius: 8px;
  font-size: 19px;
  border: 1px #eee solid;
  transition: 0.3s;
  color: #fff; }

.amount-type li label {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 50px;
  padding: 10px;
  border-radius: 8px;
  transform: translate(-50%, -50%);
  text-align: center;
  cursor: pointer; }

.amount-type li:hover {
  transform: scale(1.2); }

[type='radio'] {
  opacity: 0; }

[type='radio']:checked ~ label {
  border: 3px solid #000; }

.input-group {
  display: flex;
  flex-direction: row;
  border: 1px solid #ccc;
  padding: 8px 10px;
  border-radius: 8px;
  margin: 12px;
  justify-content: space-between;
  width: 100%; }

form {
  display: flex;
  flex-direction: column;
  align-items: center; }

input {
  flex-grow: 2;
  border: none;
  font-size: 16px; }

input:focus {
  outline: none; }

.feedback-stats {
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.num-display {
  position: absolute;
  top: -10px;
  left: -10px; }

.close,
.edit {
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
  background: none;
  border: none; }

.edit {
  right: 40px; }

.btn {
  color: #fff;
  border: 0;
  border-radius: 8px;
  color: #fff;
  width: 120px;
  height: 40px;
  font-size: 18px;
  cursor: pointer; }

.btn-primary {
  background-color: #202142; }

.btn-secondary {
  background: #ff6a95; }

.btn:hover {
  transform: scale(0.98);
  opacity: 0.9; }

.btn:disabled {
  background-color: #cccccc;
  color: #333;
  cursor: auto; }

.btn:disabled:hover {
  transform: scale(1);
  opacity: 1; }

.message {
  padding-top: 10px;
  text-align: center;
  color: rebeccapurple; }

.budget-numbers {
  display: flex;
  justify-content: space-between; }

@media (max-width: 600px) {
  .amount-type li {
    margin: 10px 3px; }
  .amount-type {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 30px 0 40px; }
  .input-group input {
    width: 80%; } }
